@import url(http://weloveiconfonts.com/api/?family=entypo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes slide-in-right {
  from {
    transform: translateX(1000px) rotate(2000deg);
    opacity: 0%;
  }
  to {
    transform: translateX(0px) rotate(0deg);
    opacity: 100%;
  }
}
@-webkit-keyframes slide-in-right {
  from {
    transform: translateX(1000px) rotate(2000deg);
    opacity: 0%;
  }
  to {
    transform: translateX(0px) rotate(0deg);
    opacity: 100%;
  }
}

.slide-in-word {
  -webkit-animation: slide-in-right 3s forwards;
  animation: slide-in-right 3s forwards;
  position: relative;
  display: inline-block;
}

div.race-btn {
  text-align: center;
  margin-top: 10px;
}

.song-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 500px;
  margin: 0 auto;
  padding-top: 200px;
}

.typing-input {
  margin: 0 auto;
  width: 500px;
}

div.words-to-type {
  margin: 0 auto;
  width: 700px;
  font-size: 1.25rem;
}

div.prev-words {
  text-align: center;
  font-size: 1.25rem;
  margin-top: 50px;
  margin-bottom: -50px;
}

div.wpm-corner {
  font-size: 3rem;
  bottom: 10px;
  position: fixed;
  left: 10px;
}

div.final-words {
  text-align: center;
  margin-top: 100px;
}

div.info {
  margin: 0 auto;
  text-align: center;
  font-size: 1.25rem;
  width: 1000px;
}

div.page-btn {
  text-align: center;
}

.questions {
  font-size: 10px;
  text-align: center;
  position: fixed;
  bottom: 0rem;
}

div.error { 
  color: red;
}

span{
  margin: .3rem;
}

@keyframes slide-out-left {
  from {
    transform: translateY(0px) rotate(0deg);
    opacity: 100%;
  }
  to {
    transform: translateY(-100px) rotate(-720deg);
    opacity: 0%;
  }
}
@-webkit-keyframes slide-out-left {
  from {
    transform: translateY(0px) rotate(0deg);
    opacity: 100%;
  }
  to {
    transform: translateY(-100px) rotate(-720deg);
    opacity: 0%;
  }
}

span.good {
  -webkit-animation: slide-out-left 2s forwards;
  animation: slide-out-left 2s forwards;
  display: inline-block;
}

.input-container {
  position: fixed;
  margin-top: 10%;
  left: 25%;
  bottom: 0;
  padding-bottom: 1%;
}

.light {
  background-color: rgb(153, 255, 204, 0.5);
  color: rgb(0, 0, 0, 1);
}

.dark {
  background-color: rgb(0, 0, 0, 0.7);
  color: rgb(153, 255, 204, 1);
}
*, *:before, *:after {
  margin:0;
  padding:0;
  box-sizing:border-box;
}
#lamp {
  position:relative;
  width:100vw;
  height:30vh;
  overflow:hidden;
}
input[name="switch"], input[name="switch"] + label {
  position:absolute;
  width:4rem;
  height:2rem;
}
input[name="switch"] + label {right:3rem;}
input[name="switch"] {
  opacity:0;
  z-index:9;
  cursor:pointer;
}
input[value="on"] {
  right:3rem;
}
input[value="off"] {
  right:-4rem;
}
input[name="switch"] + label {
  text-align:center;
}
[class*="entypo-"]:before {
  line-height:4rem;
  font-size:2.5rem;
  color:rgba(0,0,0,0.4);
  font-family:'entypo', sans-serif;
}
/* input[value="on"]:checked {
  right:-4rem;
} */
/* input[value="on"]:checked + input[value="off"] {
  right:3rem;
} */
.lamp {
  position:relative;
  margin:0 auto;
  width:.7rem;
  height:5rem;
  background-image:linear-gradient(rgba(19, 11, 11, 0.9), rgba(0,0,0,0.9)),
                   linear-gradient(rgba(0,0,0,1), rgba(0,0,0,1)),
                   linear-gradient(rgba(0,0,0,1), rgba(0,0,0,1));
  background-repeat:no-repeat;
  background-size: .15rem 8rem, .4rem .8rem, .7rem 2rem;
  background-position:60% 0, .19rem 3rem, 0 3.8rem;
}
.lamp:before, .lamp:after {
  content:'';
  position:absolute;
}
.lamp:before {
  left:-1.54rem;
  bottom:-4rem;
  width:4rem;
  height:4rem;
  border-radius:50%;
  background:rgba(255,255,255,0.03);
  box-shadow:inset 2px -2px 10px rgba(255,255,255,0.07);
  transition:all .15s;
}
.gonna-give-light, 
.gonna-give-light:before{
  position:absolute;
}
.gonna-give-light {
  top:5.3rem;
  left:.25rem;
  width:0;
  height:1.5rem;
  border-right:.2rem solid rgba(255,255,255,0.05);
}
.gonna-give-light:before {
  content:'';
  top:1.5rem;
  left:-.35rem;
  width:.9rem;
  height:.9rem;
  border-radius:50%;
  border:.2rem solid rgba(255,255,255,0.05);
  box-shadow:0px 0px 50px rgba(255,255,255,0);
}
